import Signets from './Signets';
import './ListeSignets.scss';
import tabSignets from './data/signets.json';

export default function ListeSignets(props) {
    return (
        <div div className="ListeSignets">
            <ul>
                {/* On va générer les signets à partir de la structure obtenue du fichier JSON */}
                
                {tabSignets.map(signets => 
                    <Signets titre={signets.titre} date_modif={signets.date_modif} id={signets.id} couleur={signets.couleur} key={signets.id}/>
                )}               
            </ul>
        </div>
    );
}