import './Signets.scss';

export default function Signets(props) {
    return(
        <li className="Signet">
            <img src={'images-signets/' + props.id + '.webp'} alt=""></img>
            <div className="info">
                <p className="titre">{props.titre}</p>
                <p className="date_modif">{props.date_modif}</p>
                <p className="couleur">{props.couleur}</p>
            </div>
        </li>
    );
}